import request from '@/utils/request'

export function fetchList(params) {
  return request({
    url: '/api/server',
    method: 'get',
    params: params
  })
}

export function testServer(id) {
  return request({
    url: '/api/server/test/' + id,
    method: 'get'
  })
}

export function createServer(data) {
  return request({
    url: '/api/server',
    method: 'post',
    data: data
  })
}

export function updateServer(id, data) {
  return request({
    url: '/api/server/' + id,
    method: 'put',
    data: data
  })
}

export function download(params) {
  return request({
    url: '/api/server/download',
    method: 'get',
    params: params
  })
}

export function deleteServer(id) {
  return request({
    url: '/api/server/' + id,
    method: 'delete'
  })
}

export function deleteServers(ids) {
  return request({
    url: '/api/server',
    method: 'delete',
    data: ids
  })
}



